import { OneAsset } from "./OneAsset";

export const MyAssets = ({ currentAssets, setCurrentAssets }) => {
  return (
    <div
      style={{
        textAlign: "left",
        display: "grid",
        gridGap: "0.2em"
      }}
    >
      <h2>My Assets</h2>
      {currentAssets.currencies.length > 0 ? (
        currentAssets.currencies.map((oneCurrency) => (
          <OneAsset
            key={oneCurrency.id}
            oneCurrency={oneCurrency}
            setCurrentAssets={setCurrentAssets}
          />
        ))
      ) : (
        <p>No assets, why not buy some?</p>
      )}
    </div>
  );
};
