import "./styles.css";
import { MyAssets } from "./MyAssets";
import { useState } from "react";

const initialState = {
  bankUSD: 10000,
  currencies: [
    { id: 1, name: "eth", amount: 2.0, conversionUSD: 3500 },
    { id: 2, name: "sol", amount: 100, conversionUSD: 200 }
  ]
};

export default function App() {
  const [currentAssets, setCurrentAssets] = useState(initialState);
  return (
    <div className="App">
      <h1>Welcome to Coinbase</h1>
      <h4>Current Bank: ${currentAssets.bankUSD}</h4>
      <MyAssets
        currentAssets={currentAssets}
        setCurrentAssets={setCurrentAssets}
      />
    </div>
  );
}
