import { useState } from "react";

export const BuySell = ({ actionType, currentCurrency, setCurrentAssets }) => {
  const [modifyAmount, setModifyAmount] = useState(0);
  let actionName = "";
  if (actionType === "BUY") {
    actionName = "Buy";
  } else if (actionType === "SELL") {
    actionName = "Sell";
  }
  if (actionType !== "BUY" && actionType !== "SELL") {
    return <p>Invalid BuySell Action</p>;
  }

  return (
    <div
      style={{
        display: "grid",
        gridTemplateColumns: "1fr 1fr"
      }}
    >
      <input
        type="number"
        min="0"
        value={modifyAmount}
        onChange={(e) => {
          setModifyAmount(parseFloat(e.target.value));
        }}
      />
      <button
        onClick={() => {
          let cryptoDelta = 0;
          if (actionType === "BUY") {
            cryptoDelta = modifyAmount;
          } else if (actionType === "SELL") {
            cryptoDelta = -modifyAmount;
          }
          const usdDelta = -cryptoDelta * currentCurrency.conversionUSD;

          if (modifyAmount > 0) {
            setCurrentAssets((currState) => {
              const updatedCurrencies = currState.currencies.map(
                (oneCurrency) => {
                  if (oneCurrency.id === currentCurrency.id) {
                    return {
                      ...oneCurrency,
                      amount: oneCurrency.amount + cryptoDelta
                    };
                  }
                  return oneCurrency;
                }
              );

              return {
                ...currState,
                currencies: updatedCurrencies,
                bankUSD: currState.bankUSD + usdDelta
              };
            });
            setModifyAmount(0);
          }
        }}
      >
        {actionName}
      </button>
    </div>
  );
};
